<template>
	<div class="body">
		<div class="search_group">
			<div class="search_group_top flex_start">
				<div class="search_box">
					<el-input v-model="petfund_search_content" prefix-icon="el-icon-search" placeholder="自管基金全称" clearable>
					</el-input>
				</div>
				<div class="search_box">
					<el-input v-model="event_search_content" placeholder="回款事件名称" clearable>
					</el-input>
				</div>
				<div class="btn_box">
					<button @click="getInitData(1)">搜索</button>
				</div>
			</div>
		</div>
		<div class="table_title flex_between">
			<div class="">
				自管基金回款总数<span>(&nbsp;{{ total }}&nbsp;)</span>
			</div>
			<div class="flex_end">
				<button class="blue_btn" @click="downloadExcel">
					<span class="iconfont icon-daoru"></span>
					导出excel表格
				</button>
			</div>
		</div>
		<div class="table">
			<el-table :data="tableData" border style="width: 100%">
				<el-table-column sortable prop="id" label="序号" width="80">
					<template slot-scope="scope">
						{{ (scope.$index + 1) + (currentPage - 1) * pageSize }}
					</template>
				</el-table-column>
				<el-table-column sortable prop="fof_name" label="自管基金全称" width="220">
				</el-table-column>
				<el-table-column sortable prop="fof_id" label="ID" width="80">
				</el-table-column>
				
				<el-table-column sortable prop="num" label="编号" width="80">
				</el-table-column>
				<el-table-column sortable prop="event" label="回款事件名称" width="300">
				</el-table-column>
				<el-table-column sortable prop="receive_date" label="到账日期" width="180">
				</el-table-column>
				<el-table-column sortable prop="fund_name" label="标的名称" width="220">
				</el-table-column>
				<el-table-column sortable prop="backTypeName" label="回款类型" width="180">
				</el-table-column>
				<el-table-column sortable prop="back" label="回款返本（万元）" width="180">
				</el-table-column>
				<el-table-column sortable prop="profile" label="回款收益-税后（万元）" width="200">
				</el-table-column>
				<el-table-column sortable prop="payback_detail" label="分配详情" width="180">
				</el-table-column>
				
				<template slot="empty">
					<div class="table-nodata flex_start">
						<img src="../../images/table_nodata.png">
						<span>没有对应数据</span> 
					</div>
				</template>
			</el-table>
			<el-pagination v-if="total" @size-change="everyPageSizeChange" prev-text="上一页" next-text="下一页" background @current-change="changeCurrentPage" :current-page="currentPage" :page-size="pageSize" layout="sizes, prev, pager, next, jumper" :total="total">
			</el-pagination>
		</div>
	</div>
</template>

<script>
	import { dialogConfirm, dialogConfirmHasbtn } from '../../config/mUtils'
	import { getBackListReportSelfApi,downloadBackExcelReportSelfApi } from '../../config/api'
	
	export default {
		name: 'selfFundBackTotal',
		data() {
			return {
				petfund_search_content: '',
				event_search_content: '',
				currentPage: 1,
				pageSize: 10,
				total: 0,
				
				tableData: [ ]
			}
		},
		created() {
			this.getInitData()
		},
		mounted() {},
		components: {},
		methods: {
			getInitData(is_init_page){
				if (is_init_page) {
					this.currentPage = 1;
				}
				getBackListReportSelfApi({
					petfund_search_content: this.petfund_search_content,
					event_search_content: this.event_search_content,
					page: this.currentPage,
					size: this.pageSize
				}).then(res => {
					//console.log(res);
					if(res.data.msgNo == 1) {
						this.tableData = res.data.payload.data;
						this.total = res.data.payload.count;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			downloadExcel(){
				downloadBackExcelReportSelfApi({
					
				}).then(res => {
					//console.log(res);
					if(res.data.msgNo == 1) {
						window.location.href = res.data.payload.url;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			
			// 分页功能模块
			changeCurrentPage(e){
				this.currentPage = e;
				this.getInitData()
			},
			everyPageSizeChange(e){
				this.pageSize = e;
				this.getInitData()
			},
		},
	}
</script>

<style scoped="scoped">
	.body{
		padding: 0 !important;
	}
	.search_group{
		padding: 30px 0;
    		border-bottom: 1px solid #F1F1F1;
	}
	.search_group_bottom{
		margin-top: 20px;
	}
	.search_group_bottom .search_box{
		width: 140px;
	}
	
	.search_box{
		width: 170px;
		height: 30px;
		margin-right: 13px;
	}
	.date_box{
		width: 162px;
	}
	.date_middle_text{
		margin: 0 10px;
	}
	.btn_box{
		margin-left: 24px;
	}
	.btn_box button{
		width: 80px;
		height: 30px;
		color: white;
		font-size: 14px;
		border-radius: 20px;
		background: #6292F4;
	}
	.table_title{
		margin: 26px 0;
		color: #6292F4;
		font-size: 14px;
	}
	.table_title span{
		color: #6292F4;
	}
</style>


